'use client';

import React, { createContext, useContext, useReducer } from 'react';
import { User } from '@/types';
import { signInWithGoogle as googleSignIn } from '@/lib/supabase/auth';

interface AuthState {
  user: User | null;
  token: string | null;
  loading: boolean;
  error: string | null;
}

interface AuthAction {
  type: string;
  payload?: any;
}

// Action types
const AUTH_ACTIONS = {
  SET_USER: 'SET_USER',
  SET_TOKEN: 'SET_TOKEN',
  SET_LOADING: 'SET_LOADING',
  SET_ERROR: 'SET_ERROR',
  LOGOUT: 'LOGOUT'
};

// Initial state
const initialState: AuthState = {
  user: null,
  token: null,
  loading: false,
  error: null
};

// Reducer
const authReducer = (state: AuthState, action: AuthAction): AuthState => {
  switch (action.type) {
    case AUTH_ACTIONS.SET_USER:
      return {
        ...state,
        user: action.payload
      };
    case AUTH_ACTIONS.SET_TOKEN:
      return {
        ...state,
        token: action.payload
      };
    case AUTH_ACTIONS.SET_LOADING:
      return {
        ...state,
        loading: action.payload
      };
    case AUTH_ACTIONS.SET_ERROR:
      return {
        ...state,
        error: action.payload
      };
    case AUTH_ACTIONS.LOGOUT:
      return {
        ...state,
        user: null,
        token: null
      };
    default:
      return state;
  }
};

// Create context
export const AuthContext = createContext<{
  user: User | null;
  token: string | null;
  loading: boolean;
  error: string | null;
  setUser: (user: User | null) => void;
  setToken: (token: string | null) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  logout: () => void;
  signInWithGoogle: () => Promise<void>;
} | undefined>(undefined);

// Provider component
interface AuthProviderProps {
  children: React.ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [state, dispatch] = useReducer(authReducer, initialState);

  const setUser = (user: User | null) => {
    dispatch({ type: AUTH_ACTIONS.SET_USER, payload: user });
  };

  const setToken = (token: string | null) => {
    dispatch({ type: AUTH_ACTIONS.SET_TOKEN, payload: token });
  };

  const setLoading = (loading: boolean) => {
    dispatch({ type: AUTH_ACTIONS.SET_LOADING, payload: loading });
  };

  const setError = (error: string | null) => {
    dispatch({ type: AUTH_ACTIONS.SET_ERROR, payload: error });
  };

  const logout = () => {
    dispatch({ type: AUTH_ACTIONS.LOGOUT });
  };

  const signInWithGoogle = async () => {
    try {
      setLoading(true);
      setError(null);

      // Dynamically import supabase client to avoid SSR issues
      const { createClientComponentClient } = await import('@supabase/auth-helpers-nextjs');
      const supabase = createClientComponentClient();

      // Check if window is available (client-side only)
      if (typeof window === 'undefined') {
        throw new Error('Authentication can only be performed on the client side');
      }

      // Use Supabase Google sign-in
      const { data, error } = await supabase.auth.signInWithOAuth({
        provider: 'google',
        options: {
          redirectTo: `${window.location.origin}/auth/callback`
        }
      });

      if (error) {
        throw error;
      }

      // The OAuth flow will redirect the user to Google for authentication
      // The actual session handling happens in the callback
    } catch (error: any) {
      console.error('Google sign-in error:', error);
      setError(error.message || 'Failed to sign in with Google. Please try again.');
      throw error; // Re-throw so calling components can handle the error
    } finally {
      setLoading(false);
    }
  };

  return (
    <AuthContext.Provider
      value={{
        user: state.user,
        token: state.token,
        loading: state.loading,
        error: state.error,
        setUser,
        setToken,
        setLoading,
        setError,
        logout,
        signInWithGoogle
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};